import AppController from '../controller/controller';
import { AppView } from '../view/appView';
import {dataSources, dataNews,article, sourceData} from '../general/types';

class App {
    private controller: AppController;
    private view: AppView;
    constructor() {
        this.controller = new AppController();
        this.view = new AppView();
    }

    start() {
        document
            .querySelector('.sources')
            .addEventListener('click', (e) => this.controller.getNews(e, <dataNews>(data:dataNews) => this.view.drawNews(data)));
        this.controller.getSources(<dataSources>(data:dataSources):void =>  this.view.drawSources(data));
    }
}

export default App;
