import './sources.css';
import {sourceData} from '../../general/types';

class Sources {
    draw(data:sourceData[]) {
        const fragment:DocumentFragment = document.createDocumentFragment();
        const sourceItemTemp:Element = document.querySelector('#sourceItemTemp');

        data.forEach((item) => {
            const sourceClone:HTMLElement = (sourceItemTemp as HTMLTemplateElement).content.cloneNode(true) as HTMLElement;//((sourceItemTemp.innerHTML as unknown as HTMLElement).cloneNode(true)) as HTMLElement;

            sourceClone.querySelector('.source__item-name').textContent = item.name;
            sourceClone.querySelector('.source__item').setAttribute('data-source-id', item.id);

            fragment.append(sourceClone);
        });

        document.querySelector('.sources').append(fragment);
    }
}

export default Sources;
