import News from './news/news';
import Sources from './sources/sources';
import {dataSources, dataNews,article, sourceData} from '../general/types'

export class AppView {
    
    private news:News;
    private sources:Sources;

    constructor() {
        this.news = new News();
        this.sources = new Sources();
    }

    drawNews(data?:dataNews):void {
        const values:article[] = data.articles ? data.articles : new Array() as article[];
        this.news.draw(values);
    }

    drawSources(data?:dataSources):void {
        const values:sourceData[] = data.sources ? data.sources : new Array() as [sourceData];
        this.sources.draw(values);
    }
}

export default AppView;
