import {option, callbackLoaderType,getRespArg,dataSources,dataNews} from '../general/types'
class Loader {
    private baseLink:string;
    private options: option;
    constructor(baseLink:string, options:option) {
        this.baseLink = baseLink;
        this.options = options;
    }

    getResp(
        endpoint:getRespArg,
        callback = <T> (data?:T):void => {
            console.error('No callback for GET response');
        }
    ):void {
        this.load('GET', endpoint.endpoint, callback, endpoint.options);
    }

    errorHandler(res:Response):Response {
        if (!res.ok) {
            if (res.status === 401 || res.status === 404)
                console.log(`Sorry, but there is ${res.status} error: ${res.statusText}`);
            throw Error(res.statusText);
        }

        return res;
    }

    makeUrl(options:option, endpoint:string):string {
        const urlOptions:{[index:string]:string} = { ...this.options, ...options };
        let url = `${this.baseLink}${endpoint}?`;
        
        Object.keys(urlOptions).forEach((key:string) => {
            url += `${key}=${urlOptions[key]}&`;
        });

        return url.slice(0, -1);
    }

    load(method:string, endpoint:string, callback:callbackLoaderType, options:option) {
        fetch(this.makeUrl(options, endpoint), { method })
            .then(this.errorHandler)
            .then((res:Response) => {
                return res.json()
            })
            .then((data:Response) =>{ 
                return callback(data)
            })
            .catch((err:Error) => console.error(err));
    }
}

export default Loader;
